import { useQuery, gql } from '@apollo/client';

// material-ui
import { makeStyles, Theme } from '@material-ui/core/styles';
import {
	IconButton,
	Table,
	TableBody,
	TableCell,
	TableContainer,
	TableRow,
	TableHead,
} from '@material-ui/core';
import { visuallyHidden } from '@material-ui/utils';

// project imports
import _isEmpty from 'lodash.isempty';
import _get from 'lodash.get';

// assets
import VisibilityTwoToneIcon from '@material-ui/icons/VisibilityTwoTone';
import EditTwoToneIcon from '@material-ui/icons/EditTwoTone';
import { HeadCell } from '../../../types';
import MainCard from '../../../ui-component/cards/MainCard';
import Chip from '../../../ui-component/extended/Chip';
import config from '../../../config';
import Loader from '../../../ui-component/Loader';
import { listForms } from '../../../graphql/queries';

// style constant
const useStyles = makeStyles((theme: Theme) => ({
	root: {
		width: '100%',
	},
	paper: {
		width: '100%',
		marginBottom: theme.spacing(2),
	},
	table: {
		minWidth: 750,
	},
	sortSpan: { ...visuallyHidden },
}));

// -----------------------|| Form LIST ||-----------------------//
// table header options
const headCells: HeadCell[] = [
	{
		id: 'id',
		numeric: false,
		label: 'UUID',
		align: 'left',
	},
	{
		id: 'title',
		numeric: false,
		label: 'Title',
		align: 'left',
	},
	{
		id: 'Category',
		numeric: false,
		label: 'Category',
		align: 'right',
	},
	{
		id: 'FormType',
		numeric: false,
		label: 'Form Type',
		align: 'center',
	},
	{
		id: 'created-date',
		numeric: false,
		label: 'Created Date',
		align: 'center',
	},
	{
		id: 'updated-date',
		numeric: false,
		label: 'Updated Date',
		align: 'center',
	},
	{
		id: 'action',
		numeric: false,
		label: 'Actions',
		align: 'center',
	},
];

const FormList = () => {
	const classes = useStyles();

	const { loading, error, data } = useQuery(
		gql`
			${listForms}
		`,
		{
			variables: {
				limit: config.pagination.limit,
			},
		}
	);

	if (loading) return <Loader />;
	if (error) return <p>Error :(</p>;
	if (_isEmpty(data)) return <></>;

	const items = _get(data, ['listForms', 'items']);

	return (
		<MainCard title='Form List' content={false}>
			{/* table */}
			<TableContainer>
				<Table className={classes.table} aria-labelledby='tableTitle'>
					<TableHead>
						<TableRow>
							{headCells.map((headCell, index) => (
								// eslint-disable-next-line react/no-array-index-key
								<TableCell key={index}>{headCell.label}</TableCell>
							))}
						</TableRow>
					</TableHead>
					<TableBody>
						{items.map((row: any, index: number) => (
							// eslint-disable-next-line react/no-array-index-key
							<TableRow hover role='checkbox' tabIndex={-1} key={index}>
								<TableCell>{row.FormId}</TableCell>
								<TableCell align='right'>{row.Title}</TableCell>
								<TableCell align='center'>{row.date}</TableCell>
								<TableCell align='center'>
									{row.status === 1 && (
										<Chip label='Complete' size='small' chipcolor='success' />
									)}
									{row.status === 2 && (
										<Chip label='Processing' size='small' chipcolor='orange' />
									)}
									{row.status === 3 && (
										<Chip label='Confirm' size='small' chipcolor='primary' />
									)}
								</TableCell>
								<TableCell align='center' sx={{ pr: 3 }}>
									<IconButton color='primary'>
										<VisibilityTwoToneIcon sx={{ fontSize: '1.3rem' }} />
									</IconButton>
									<IconButton color='secondary'>
										<EditTwoToneIcon sx={{ fontSize: '1.3rem' }} />
									</IconButton>
								</TableCell>
							</TableRow>
						))}
					</TableBody>
				</Table>
			</TableContainer>
		</MainCard>
	);
};
export default FormList;
